<template>
    <div>
        <div class="box">
            <div class="header" ref="header">
                <div class="arrow-left fl" @click="back" >            
                    <van-icon name="arrow-left" size="30px"/>                  
                </div>

                <span class="header-title">阅读历史</span>
                <div class="right-box fr">
                    <div class="search fl">
                        <van-icon name="search" size="30px"/>
                    </div>

                    <div class="ellipsis fl">
                        <van-icon name="ellipsis" size="30px"/>
                    </div>
                </div>
            </div>

            <div class="list-box" ref="listBox" @scroll="load">

                <div class="content" ref="content" v-for="(item,index) in data" :key="index" @click="newsDetails(item.item_id,item)">
                    <div class="header clearfix">
                        <span class="fl">来源:{{item.source}}</span>
                        <span class="fl tag">{{item.chinese_tag}}</span>
                        <span class="fr" @click.stop="remove(index)">删除</span>
                    </div>
                    <div class="news-content">
                        <div class="img fl">
                            <img class="auto-img" :src="item.middle_image" alt="">
                        </div>
                        <div class="text fr">
                            <div class="title">{{item.title}}</div>

                            <div class="abstract">{{item.abstract}}</div>
                        </div>
                    </div>
                </div>

            </div>      
        </div>
        <div>
             <router-view />
        </div>
    </div>
</template>

<script>
    export default {
        name:'collection',
        data(){
            return {
                alldata:[],

                data:[],

                count:5,

                isLoad:true


            }
        },

        created(){

        // let item = this.$route.params.id;
        // console.log('item==>',item)

        // this.data.unshift(item) 
        // console.log('this.data==>',this.data)

        this.alldata = localStorage.getItem('historyData')

        this.alldata = JSON.parse(this.alldata)
        console.log('this.alldata==>',this.alldata)

        this.data = this.alldata.slice(0,this.count)

        this.count +=5



        },

        methods:{

            // 懒加载
            load(){
                let _this = this

                //保存当前滚动的所有定时器序号
                var timers = [];

                var clearTime = setTimeout(function () {
                    for (var i = 1; i < timers.length; i++) {
                        clearTimeout(timers[i]);
                    }

                    let headerHeight = _this.$refs.header.offsetHeight
                    // console.log('headerHeight ==>',headerHeight)

                    // 获取最后一个节点
                    let last = _this.$refs.content[_this.$refs.content.length -1]
                    // console.log('last ==>',last)

                    // 最后一个节点可视窗口的距离
                    let lastHeight = last.getBoundingClientRect();
                    let top = parseFloat(lastHeight.top)
                    // console.log('top ==>',top)

                    // 每个内容的高度
                    let contentHeight = _this.$refs.content[0].clientHeight
                    // console.log('contentHeight ==>',contentHeight)

                    // 每个内容的高度
                    let listBox = _this.$refs.listBox.offsetHeight
                    // console.log('listBox ==>',listBox)

                    let isPass = listBox - contentHeight + headerHeight 
                    // console.log('isPass ==>',isPass)

                    if(isPass >=top){

                        _this.$toast.loading({
                            duration:0,
                            message: "加载中..."
                        });
                        
                        _this.data = _this.alldata.slice(0, _this.count)
                        _this.count +=5

                        if(_this.data.length < 5){
                            _this.isLoad = false
                        }
                        //取消加载提示
                        _this.$toast.clear();       
                    }
                    timers = [];      
                })
                timers.push(clearTime);
            },

            //返回上一级
            back(){
                this.$router.go(-1)
            },

            //删除
            remove(index){
                this.data.splice(index,1)
                localStorage.setItem('historyData',JSON.stringify(this.data))
            },

            // 跳转到详情页
            newsDetails(id,item){
                // console.log('id ==>',id)
                // this.$router.push({name:'newsDetails'})
                this.$router.push({
                    name: 'newsDetails',
                    params: {
                        id: id,
                        item:item
                    }
                })
            }
        }
        
    }
</script>

<style lang="less" scoped>
    .header{
        overflow: hidden;
        text-align: center;
    }
    .header-title{
        display: inline-block;
        margin-top: 24px;
        font-size: .5rem;
    }
    .arrow-left{
        margin-top: 24px;
        border-bottom: 1px solid #f5f5f5;
    }

    .right-box{
        margin-top: 24px;
    }

    .ellipsis{
        margin-right: .3rem;
    }
    .search{
       margin-right: .3rem; 
    }

    .list-box{
        overflow-y: auto;
        height: 16.2rem;
        background: #f5f5f5;
    }

    .content{
        margin-bottom: .3rem;
        margin-top: .3rem;

        .header{
            width:calc(100% - 1rem);
            height: .6rem;
            background: #fff;
            line-height: .6rem;
            padding: 0 .5rem;
            >span:nth-child(1){
                margin-left: .3rem;
            }
            >span:nth-child(2){
                margin-right: .3rem;
            }
            .tag{
                margin-left: 40px;
            }
        }
        .news-content{
            width:calc(100% - 1rem);
            height: 3rem;
            background: #fff;
            line-height: .8rem;
            padding: 0 .5rem;

            .img{
                width: 3rem;
                height: 2.1rem;
                background: #ddd;
                margin-top: .45rem;
            }
            .text{
                width:calc(100% - 3.3rem);
                height: 100%;

                .title{
                    font-size: .4rem;
                    font-weight: bold;
                    margin-top: .1rem;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;     
                }
                .abstract{
                    height: 1.47rem;
                    color: #595959;
                    line-height: .5rem;
                    font-size: .35rem;
                    margin-top: .3rem;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 3;
                    overflow: hidden;
                }
            }
        }
    }

</style>